body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: "LuckiestGuy-Regular";
  src: local("LuckiestGuy-Regular"),
    url("./fonts/LuckiestGuy-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "Rubik-Italic";
  src: local("Rubik-Italic-VariableFont_wght"),
    url("./fonts/Rubik-Italic-VariableFont_wght.ttf") format("truetype");
}

@font-face {
  font-family: "Rubik";
  src: local("Rubik-VariableFont_wght"),
    url("./fonts/Rubik-VariableFont_wght.ttf") format("truetype");
}
